.item--cart {
    width: 100%;
    display: flex;
    justify-content: row;
    justify-content: space-between;
    align-items: center;

    color: #111;
    font-family: 'Merriweather';
    font-size: 18px;
    line-height: 27px; 

    &__row {
        display: flex;
        justify-content: row;
        align-items: center;
        gap: 25px;
    }

    &__img {
        width: 200px;
    }
}
